body.light-theme {
  background-color: var(--white-text-color);
  color: black;
}

body.dark-theme {
  background-color: #22272e;
  color: var(--white-text-color);
}

footer.light-theme {
  background-color: #f7f7f7;
  color: #545454;
}

footer.dark-theme {
  background-color: #22272e;
  color: var(--white-text-color);
}

.toolbar {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 10px;
}

/* THEME SWITCH */
.theme-switch {
  display: flex;
  align-items: center;
}

.theme-switch__icon {
  color: var(--accent-bg-color);
  height: 15px;
  width: 15px;
  margin-right: 8px;
  margin-left: 8px;
}

.theme-switch__control {
  display: flex;
  align-items: center;
  flex-shrink: 0;
  position: relative;
  width: 55px;
  height: 23px;
  border: 2px solid var(--accent-bg-color);
  border-radius: 7px;
  padding: 3px 0;
}

.theme-switch__toggle {
  position: absolute;
  left: 0;
  top: 0;
  margin: 0;
  padding: 0;
  opacity: 0;
  height: 0;
  width: 0;
  pointer-events: none;
}

.theme-switch__track {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  color: transparent;
  user-select: none;
  background-color: #545454;
  border-radius: 5px;
  z-index: 1;
  cursor: pointer;
}

.theme-switch__marker {
  position: relative;
  background-color: #f7f7f7db;
  width: 15px;
  height: 15px;
  border-radius: 5px;
  z-index: 2;
  pointer-events: none;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.25);
  transform: translateX(3px);
  transition: transform 300ms cubic-bezier(0.4, 0, 0.2, 1);
}

.theme-switch__toggle:checked + .theme-switch__track {
  background-color: var(--white-text-color);
}

.theme-switch__toggle:checked ~ .theme-switch__marker {
  transform: translateX(34px);
  background: var(--grey-text-color);
}

.theme-switch__toggle:focus + .theme-switch__track,
.theme-switch__toggle:active + .theme-switch__track {
  box-shadow: 0 0 0 1px var(--grey-text-color);
}
